import React from 'react'

import NavBar from '../components/NavBar'
import PriceTable from '../components/PriceTable'
import Footer from '../components/Footer'

const PlanPrices = () => {
    return (
        <div>
            <NavBar />
            <div className="container">
                <PriceTable />
            </div>
            <Footer />
        </div>
    )
}

export default PlanPrices